.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.login-box {
  background: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
}

.login-box h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #333;
}

.login-box input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

.login-box input:focus {
  outline: none;
  border-color: #667eea;
}

.login-box button {
  width: 100%;
  padding: 12px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s;
}

.login-box button[type="submit"] {
  background: #667eea;
  color: white;
}

.login-box button[type="submit"]:hover {
  background: #5568d3;
}

.google-btn {
  background: #4285f4;
  color: white;
}

.google-btn:hover {
  background: #357ae8;
}

.forgot-password {
  background: transparent;
  color: #667eea;
  text-decoration: underline;
}

.forgot-password:hover {
  color: #5568d3;
}

.error-message {
  background: #fee;
  color: #c33;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  text-align: center;
}

.success-message {
  background: #efe;
  color: #3c3;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  text-align: center;
}

.toggle-auth {
  text-align: center;
  margin-top: 20px;
  color: #666;
}

.toggle-auth span {
  color: #667eea;
  cursor: pointer;
  font-weight: bold;
}

.toggle-auth span:hover {
  text-decoration: underline;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
